import React, { Component } from 'react';
import Head from '../components/Head';
import axios from 'axios';
import config from '../config';
class Index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            arrDares: [],
            currentDare: '',
            loading: false
        };
    }
    componentDidMount() {
        this.setState({ loading: true })
        const query = `https://graph.facebook.com/${config.post_id}/comments?fields=message,from&access_token=${config.access_token}`
        this.getAllComments(query)
    }
    getAllComments = (query) => {
        axios.get(query)
            .then(res => {
                const resData = res.data
                const { data, paging } = resData
                this.setState({ arrDares: this.state.arrDares.concat(data) })
                if (paging && paging.next) {
                    this.getAllComments(paging.next)
                } else {
                    this.setState({ loading: false })
                }
            })

    }
    generateDare = () => {
        if (this.state.loading) return
        const random = Math.floor(Math.random() * this.state.arrDares.length)
        this.setState({ currentDare: this.state.arrDares[random] })
    }
    render() {
        const { currentDare, loading } = this.state
        const fromName = currentDare.from ? currentDare.from.name : ''
        const style = {
            container: {
                height: '90vh',
            },
            tren: {
                width: '100%',
                height: '70%',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                backgroundColor: 'blue'
            },
            duoi: {
                width: '100%',
                height: '30%',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                backgroundColor: 'green',
            }
        }
        return (
            <div className="">
                <Head></Head>
                <div style={style.container} >
                    <h5 className="center-align">Dev by @xuanhuyjs</h5>
                    <div style={style.tren}>
                        <div>
                            <div className="card blue-grey darken-1">
                                <div className="card-content white-text">
                                    <p>{currentDare.message || 'sss'}</p>
                                </div>
                                <div className="card-action right-align">
                                    <span>{fromName}</span>
                                </div>
                            </div>
                        </div>

                    </div>
                    <div style={style.duoi} className="center-align" onClick={this.generateDare} >
                        <a className="waves-effect waves-light btn-large">Button</a>
                    </div>
                </div>

            </div>
        );
    }
}

export default Index;
